import { BrowserModule } from '@angular/platform-browser';
import { HttpClientModule } from '@angular/common/http';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { AppComponent } from './app.component';
import {HashLocationStrategy, LocationStrategy} from '@angular/common';
import { HomeComponent } from './home/home.component';
import { AboutUsComponent } from './about-us/about-us.component';
import { AboutUsLegacyComponent } from './about-us-legacy/about-us-legacy.component';
import { AboutUsLeadershipComponent } from './about-us-leadership/about-us-leadership.component';
import { AboutUsPartnershipComponent } from './about-us-partnership/about-us-partnership.component';
//.. other imports
import {enableProdMode} from '@angular/core';
import { ContactUsComponent } from './contact-us/contact-us.component';
import { AboutUsCsrComponent } from './about-us-csr/about-us-csr.component';
import { AboutUsTechnologyComponent } from './about-us-technology/about-us-technology.component';
import { FormGroup, FormControl, FormsModule, Validators, FormBuilder, ReactiveFormsModule  } from '@angular/forms';
import {platformBrowserDynamic} from '@angular/platform-browser-dynamic';

enableProdMode();

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    AboutUsComponent,
    AboutUsLegacyComponent,
    AboutUsLeadershipComponent,
    AboutUsPartnershipComponent,
    AboutUsCsrComponent,
    AboutUsTechnologyComponent,
    ContactUsComponent,
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    FormsModule,
    ReactiveFormsModule,
    RouterModule.forRoot([
      {
        path: '',
        redirectTo: 'home',
        pathMatch: 'full'
      },
      {
        path:"home",
        component:HomeComponent
      },
      {
        path:"about-us",
        component:AboutUsComponent
      },
      {
        path:"about-us-legacy",
        component:AboutUsLegacyComponent
      },
      {
        path:"about-us-leadership",
        component:AboutUsLeadershipComponent
      },
      {
        path:"about-us-partnership",
        component:AboutUsPartnershipComponent
      },
      {
        path:"about-us-csr",
        component:AboutUsCsrComponent
      },
      {
        path:"about-us-technology",
        component:AboutUsTechnologyComponent
      },
      {
        path:"contact-us",
        component:ContactUsComponent
      }
    ])
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }


platformBrowserDynamic().bootstrapModule(AppModule);



