import { AfterViewInit ,Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';

declare var $:any, jQuery:any;

@Component({
  selector: 'app-about-us-partnership',
  templateUrl: './about-us-partnership.component.html',
  styleUrls: ['./about-us-partnership.component.css']
})
export class AboutUsPartnershipComponent implements OnInit, AfterViewInit {
    _url="../../api/partnership.json";
    _listOfSlider:any;

    constructor(private http:HttpClient) { }

    ngOnInit() {
        // Calling API
        this.http.get(this._url).subscribe((data) => {
          this._listOfSlider = data;
        })

        setTimeout(function(){
        // About-us partners
        $('#jwf-slider').owlCarousel({
            navigation: true,
            navText: ["<i class='icon64 icon-nav-left'></i>","<i class='icon64 icon-nav-right'></i>"],
            stagePadding: 40,
            loop:true,
            margin:10,
            nav:true,
            autoplay:true,
            autoplaySpeed:1000,
            autoplayHoverPause:true,
            navSpeed:1000,
            items:1,
            lazyLoad: true,
            responsive:{
                320:{
                    items:1,
                    stagePadding: 10
                },
                667:{
                    items:1,
                    stagePadding: 60
                },
                668:{
                    items:1,
                    stagePadding: 60
                },
                1024:{
                    items:1,
                    stagePadding: 100
                },
                1366:{
                    items:1,
                    stagePadding: 200
                },
                1800:{
                    items:1,
                    stagePadding: 400
                }
            }
        })
        }, 1000);
    }
    ngAfterViewInit(){

    }
}



