import { AfterViewInit, Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { SeniorManagement } from '../management-service.service';
import { ManagementServiceService } from '../management-service.service';

declare var $:any, jQUery:any;

@Component({
  selector: 'app-about-us-leadership',
  templateUrl: './about-us-leadership.component.html',
  styleUrls: ['./about-us-leadership.component.css'],
  providers: [ManagementServiceService]
})
export class AboutUsLeadershipComponent implements OnInit, AfterViewInit {

  _listOfMember:any;
  _url = "../../api/members.json";
  _listOfManagementMembers;

  constructor(private managementServiceService : ManagementServiceService, private http:HttpClient) {
      this._listOfManagementMembers = managementServiceService.getManagementList();
  }

  ngOnInit() {
      //Calling API
      this.http.get(this._url).subscribe(data => this._listOfMember = data);
  }

  ngAfterViewInit(){

  }
}



