import { Injectable, AfterViewInit, Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';

declare var $:any, jQuery:any;

@Component({
  selector: 'app-about-us',
  templateUrl: './about-us.component.html',
  styleUrls: ['./about-us.component.css']
})

export class AboutUsComponent implements OnInit, AfterViewInit {
  missionInfo:any;
  _url = "../../api/mission.json";
  constructor(private http:HttpClient){}

  ngOnInit() {
    //Calling API
    this.http.get(this._url).subscribe(data => this.missionInfo = data);

    //Overview about us
    function initSlider() {
        $('#we--pioneers').owlCarousel({
            navigation: true,
            navText: ["<i class='icon64 icon-nav-left'></i>","<i class='icon64 icon-nav-right'></i>"],
            stagePadding: 40,
            loop:true,
            margin:10,
            nav:true,
            items:1,
            autoplay:true,
            autoplaySpeed:1000,
            //autoplayHoverPause:true,
            navSpeed:1000,
            lazyLoad: true,
            onInitialized: startProgressBar,
            onTranslate: resetProgressBar,
            onTranslated: startProgressBar,
            responsive:{
                320:{
                    items:1,
                    stagePadding: 10
                },
                667:{
                    items:1,
                    stagePadding: 60
                },
                668:{
                    items:1,
                    stagePadding: 60
                },
                1024:{
                    items:1,
                    stagePadding: 100
                },
                1366:{
                    items:1,
                    stagePadding: 200
                },
                1800:{
                    items:1,
                    stagePadding: 400
                }
            }
        })
    }
    //progress bar
    function startProgressBar() {
        // apply keyframe animation
        var progressWidth = $('.owl-item.active').width();
        $('.slide-progress').css({
            'width': progressWidth,
            'transition': 'width 5000ms',
            'margin':'0 auto'
        });
    }

    function resetProgressBar() {
        $('.slide-progress').css({
            'width': 0,
            'transition': 'width 0s'
        });
    }

    initSlider();

    //parallax Scroll
    $('.parallax-window').parallax();

  }


  ngAfterViewInit(){
  }



}



